	module.exports = {"data":{"markdownRemark":{"html":"<p>特别热，似乎超乎寻常。城市气候开始转热，不知是不是经济发展的结果，尤其是大量增加的空调器，排放出惊人的热量，对环境的影响可想而知。</p>\n<p>报纸报道，朝鲜金日成主席逝世，可能意味着一个新时期的开始。那边最近发生一系列问题，主要在国际社会和两个朝鲜的内部。这一事件对这些问题的发展会有什么样的冲击，要观察。也许很快就会有</p>\n<p>新的发展，从国内政治稳定的角度。</p>\n<p>傍晚下了雨，天气凉爽多了。夜间到外面去散步，得一首小诗：</p>\n<p>夜游长安街</p>\n<p>清雨忽降净京城，枝绿墙红照夜灯。信步轻盈伏串露，合手重凝振拱门。指观世事出清淡，笑看人生入深沉。过客匆匆独寂寥，方识厚意又得闻。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0120.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0120.md","frontmatter":{"title":"7月10日 星期日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0120.md absPath of file >>> MarkdownRemark"}}

